import { message } from "antd";
import { useEffect, useState } from "react"
import { courseOutsideEnum } from ".";
import { fetchCourses, ICourse, modifyCourse } from "../../services/course";


export const useCourses = (count: number) => {
    const [courses, setCourses] = useState<ICourse[]>([]);
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        setLoading(true);
        fetchCourses().then((v) => {
            const newData = v?.data_list?.map(v => {
                if (v?.course_ext_info?.course_outside) {
                    return {
                        ...v.course_ext_info,
                        course_outside: 'True',
                        id: v.id,
                        org: v.org,
                        display_name: v.display_name,
                        isModify: false,
                    }
                } else {
                    return {
                        course_grade:v.course_ext_info?.course_grade|| '',
                        course_price:v.course_ext_info?.course_price|| '',
                        course_recommend_level:v.course_ext_info?.course_recommend_level|| '',
                        course_highlight:v.course_ext_info?.course_highlight|| '',
                        id: v.id,
                        org: v.org,
                        display_name: v.display_name,
                        course_outside: 'False',
                        course_link: '',
                        isModify: false,
                    }
                }
            })
            setCourses(newData);
        }).finally(() => {
            setLoading(false);
        })
    }, [count]);

    return {
        loading,
        courses,
        setCourses,
        setLoading
    }
}

export const modifyCourseAction = async (item: ICourse) => {
    if (!item.course_grade) {
        message.error('请输入课程适用年级');
        return;
    }
    if (!item.course_price) {
        message.error('请输入课程价格');
        return;
    }
    if (!item.course_recommend_level) {
        message.error('请输入课程推荐指数');
        return;
    }
    if (!item.course_highlight) {
        message.error('请输入课程产品亮点');
        return;
    }
    if (item.course_outside === courseOutsideEnum.True && !item.course_link) {
        message.error('请输入课程跳转地址');
        return;
    }
    const params = {
        course_price: item.course_price,
        course_grade: item.course_grade,
        course_recommend_level: item.course_recommend_level,
        course_highlight:item.course_highlight,
        course_overview: item.id,
        course_outside: item.course_outside,
        course_link: item.course_link
    }
    const res = await modifyCourse(params);
    return res.executed;
}
