import React, { useState } from "react";
import { ButtonWrap, Title, Wrap, } from "./styled";
import { Table, Input, Popconfirm, Button as AntButton, message } from 'antd';
import { Button } from "../../components/Button";
import LoadingPage from "../../components/loading";
import { modifyCcssitemAction, useServices,gradeList,knowledgeList,skillTotalList } from "./action";
import { deleteCcssitem, ICcssitem } from "../../services/services";
import { CustomSelect } from "../../components/CustomSelect";


export default () => {

  const [count, setCount] = useState(0);
  const [current, setCurrent] = useState(1);
  const { data, loading, setLoading, setData } = useServices(count);
  const [skillList, setSkillList] = useState([{ key: '', value: '' }]);

  const handleChangeValue = (key: keyof ICcssitem, value: any, item: ICcssitem) => {
    console.log(key,value,item);
    const newData = data.map(v => {
      if (v.id === item.id) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setData(newData);
  }

  const columns = [
    {
      title: 'id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '知识域',
      dataIndex: 'l1_domain',
      key: 'l1_domain',
      render: (value: string, item: ICcssitem) => {
        if (item.isModify) {
          return <CustomSelect
            initValue={value}
            selectData={knowledgeList}
            onChange={(key:any) => {
              handleChangeValue('l1_domain', key, item);
              // console.log(skillTotalList[key]);
              setSkillList(skillTotalList[key])
            }}
          />
        } else {
          return knowledgeList.find(v => v.key === value)?.value;
        }
      }
    },
    {
      title: '技能项',
      dataIndex: 'l2_sub_domain',
      key: 'l2_sub_domain',
      render: (value: string, item: ICcssitem) => {
        if (item.isModify) {
          return <CustomSelect
            initValue={value}
            selectData={skillList}
            onChange={(key) => {
              handleChangeValue('l2_sub_domain', key, item)
            }}
          />
        } else {
          return (Object.values(skillTotalList).flat() as {key:string,value:string}[]).find(v => v.key === value)?.value;
        }
      }
    },
    {
      title: '年级',
      dataIndex: 'l3_grade',
      key: 'l3_grade',
      width:100,
      render: (value: string, item: ICcssitem) => {
        if (item.isModify) {
          return <CustomSelect
            initValue={value}
            selectData={gradeList}
            onChange={(key) => {
              handleChangeValue('l3_grade', key, item)
            }}
          />
        } else {
          return gradeList.find(v => v.key === value)?.value;
        }
      }
    },
    {
      title: '技能点',
      dataIndex: 'item_name',
      key: 'item_name',
      render: (value: string, item: ICcssitem) => {
        if (item.isModify) {
          return <Input
            allowClear
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('item_name', e.target.value, item) }}
          />
        } else {
          return value ? value : '-';
        }
      }
    },
    {
      title: '描述',
      dataIndex: 'item_desc',
      key: 'item_desc',
      render: (value: string, item: ICcssitem) => {
        if (item.isModify) {
          return <Input
            allowClear
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('item_desc', e.target.value, item) }}
          />
        } else {
          return value ? value : '-';
        }
      }
    },
    {
      title: '操作',
      key: 'operate',
      render: (item: ICcssitem) => {
        if (item.isModify) {
          return <>
            <Button
              text='确定'
              style={{ margin:"0 5px" }}
              onPress={async () => {
                const res = await modifyCcssitemAction(item);
                if (res) {
                  setCount(v => v + 1);
                }
              }}
            />
            <Button
              style={{ margin:"0 5px" }}
              text='取消'
              onPress={async () => {
                setCount(v => v + 1);
              }}
            />
          </>
        }
        return <>
          <Button
            text='修改'
            style={{ margin:"0 5px" }}
            onPress={async () => {
              const flag = data.some(v => v.isModify);
              if (flag) {
                message.error('请先保存当前修改或新增');
                return;
              }
              const newData = data.map(v => v.item_name === item.item_name ? { ...v, isModify: true } : v);
              setData(newData);
            }}
          />
          <Popconfirm
            style={{ margin:"0 5px" }}
            title="确定删除此CCSS知识点？"
            placement="topLeft"
            transitionName=''
            onConfirm={async () => {
              setLoading(true);
              const res = await deleteCcssitem(item.id);
              setLoading(false);
              if (res.executed) {
                setCount(v => v + 1);
              }
            }}
          >
            <AntButton>删除</AntButton>
          </Popconfirm>
        </>
      }
    },
  ];

  return (
    <Wrap>
      <Title style={{textAlign:'center'}}>CCSS知识点描述对照表</Title>
      <ButtonWrap>
        <Button onPress={async () => {
          const flag = data.some(v => v.isModify);
          if (flag) {
            message.error('请先保存当前修改或新增');
            return;
          }
          setCurrent(1);
          setData(v => [{
            id:0,
            l1_domain: "",
            l2_sub_domain: '',
            l3_grade: '',
            item_name: '',
            item_desc:'',
            isModify: true
          }, ...v,])
        }}
          text='新增CCSS知识点'
        />
      </ButtonWrap>

      <Table
        pagination={{
          hideOnSinglePage: true,
          current: current,
          onChange: (page) => {
            setCurrent(page);
          }
        }}
        rowKey={(item) => item.id}
        dataSource={data}
        columns={columns.map(v => ({ ...v, align: 'center' }))}
      />
      <LoadingPage isLoading={loading} />
    </Wrap>
  )
};
