export const useServices = (count: number) => {
  const [data, setData] = useState<ICcssitem[]>([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    fetchCcssitem()
      .then((v) => {
        const newData = v.data_list;
        newData.reverse();
        setData(newData);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [count]);

  return {
    loading,
    data,
    setData,
    setLoading,
  };
};

export const modifyCcssitemAction = async (item: ICcssitem) => {
  if (!item.l1_domain) {
    message.error("请请选择知识域");
    return;
  }
  if (!item.l2_sub_domain) {
    message.error("请选择技能项");
    return;
  }
  if (!item.l3_grade) {
    message.error("请选择年级");
    return;
  }
  if (!item.item_name) {
    message.error("请填写技能点");
    return;
  }
  const reg = /\s+/;
  if (reg.test(item.item_name)) {
    message.error("技能点不允许有空格，请重新输入");
    return;
  }
  if (!item.item_desc) {
    message.error("请填写描述");
    return;
  }
  const res = await modifyCcssitem(item);
  return res.executed;
};
