import React from "react";
import { ProductItemWrap,ProductItemTitle,CheckItem,CheckWrap } from "./styled";
import { Checkbox } from 'antd';
import { IItems } from "../../services/course";

interface IProductItem {
    title: string;
    data: IItems[];
    checkedData: number[];
    onChange: (checked: boolean, id: number) => void;
    disabled:boolean
}
export const ProductItem: React.FC<IProductItem> = ({
    data,
    checkedData,
    title,
    onChange,
    disabled
}) => {
  return (
    <ProductItemWrap>
        <ProductItemTitle>
            {title}
        </ProductItemTitle>
        <CheckWrap>
              {
                  data.map((v,i) => (
                      <CheckItem key={v.id}>
                          <Checkbox
                              checked={checkedData.includes(v.id)}
                              onChange={(e) => { onChange(e.target.checked, v.id) }}
                              disabled={disabled}
                          />
                          {v.item_name}
                      </CheckItem>
                  ))
              }
        </CheckWrap>
    </ProductItemWrap>
  );
};
