import { useEffect, useState } from "react";
import {
  fetchAllProductInfo,
  fetchProductInfo,
  IItems,
} from "../../services/course";

interface IData {
  g1Date: IItems[];
  g2Date: IItems[];
  g3Date: IItems[];
  g4Date: IItems[];
  g5Date: IItems[];
  gkDate: IItems[];
  productList: { key: string; value: string }[];
  g6Date: IItems[];
  g7Date: IItems[];
  g8Date: IItems[];
  g9_g10_items: IItems[];
  g11_g12_items: IItems[];
}

export const useAllInfo = () => {
  const [data, setData] = useState<IData>({
    g1Date: [],
    g2Date: [],
    g3Date: [],
    g4Date: [],
    g5Date: [],
    g6Date: [],
    g7Date: [],
    g8Date: [],
      g9_g10_items: [],
    g11_g12_items:[],
    gkDate: [],
    productList: [],
  });
  useEffect(() => {
    // return;
    fetchAllProductInfo().then((v) => {
      const productList = v.course_ids.map((v) => ({
        key: v.id,
        value: v.course_overview__display_name,
      }));
      setData({
        g1Date: v.g1_items,
        g2Date: v.g2_items,
        g3Date: v.g3_items,
        g4Date: v.g4_items,
        g5Date: v.g5_items,
        gkDate: v.gk_items,
        productList,
        g6Date: v.g6_items,
        g7Date: v.g7_items,
        g8Date: v.g8_items,
        g9_g10_items: v.g9_g10_items,
        g11_g12_items:v.g11_g12_items
      });
    });
  }, []);

  return { data };
};

export const useProductInfo = (product: string, disabled: boolean) => {
  const [loading, setLoading] = useState(false);
  const [checkedData, setCheckedData] = useState<number[]>([]);
  useEffect(() => {
    if (disabled && product) {
      setLoading(true);
      fetchProductInfo(product)
        .then((v) => {
          setCheckedData(v.added_items);
        })
        .finally(() => {
          setLoading(false);
        });
    }
  }, [product, disabled]);

  return {
    loading,
    checkedData,
    setCheckedData,
  };
};
