import React, { useRef, useState } from "react";
import { BtnWrap, Content, UploadWrap, Wrap, } from "./styled";
import { Upload, message } from 'antd';
import { Button } from "../../components/Button";
import LoadingPage from "../../components/loading";
import { InputList } from "../../components/InputList";


export default () => {
  const [reportData, setReportData] = useState({
    id: '',
    phone: '',
    type: ''
  })

  const reportArr = [
    {
      label: '学员ID',
      initValue: reportData.id,
      onChange: (value: string) => {
        setReportData(pre => ({
          ...pre,
          id: value
        }))
      }
    },
    {
      label: '学员手机号',
      initValue: reportData.phone,
      onChange: (value: string) => {
        setReportData(pre => ({
          ...pre,
          phone: value
        }))
      }
    },
    {
      label: '测试类型',
      type: 'select',
      initValue: reportData.type,
      selectData: [
        {
          key: '1',
          value: '测试1'
        },
        {
          key: '2',
          value: '测试2'
        },
        {
          key: '3',
          value: '测试3'
        }
      ],
      onChange: (value: string) => {
        setReportData(pre => ({
          ...pre,
          type: value
        }))
      }
    }
  ]

  const props = {
    beforeUpload: (file: File) => {
      console.log(file);
      return false;
    }
  };


  const btnArr = [
    {
      text: '取消',
      onPress: async () => {

      },
      style: {
        marginRight: '30px'
      }
    },
    {
      type: 'primary',
      text: '确定',
      onPress: async () => {

      }
    }
  ]



  return (
    <Wrap>
      <Content>

        <InputList inputData={reportArr} title='报告导入' />
        <UploadWrap>
          <Upload {...props} >
            <Button text='Click to Upload'></Button>
          </Upload>
        </UploadWrap>


        <BtnWrap>
          {btnArr.map((v, i) => (
            <Button
              key={i}
              {...v}
            />
          ))
          }
        </BtnWrap>

      </Content>
    </Wrap>
  )
};
