import { ICourseDetail } from "../pages/Students/action";
import { get, post, delRequest } from "../utils/rpc"

export interface IListItem {
    key: string,
    value: string
}

export interface IRes {
    errorCode: string,
    executed: boolean,
    message: string,
    success: boolean
}

export interface IStudent {
    id: number
    username: string
    password: string
    phone_number: string
    web_accelerator_link: string
    web_accelerator_name: string
    courses_count: number,
    isModify: boolean,
}

interface dataItem {
    phone_number: string,
    web_accelerator_link: string,
    web_accelerator_name: string,
    courses_count:number,
    user: {
        id: number
        password: string
        username: string
    }
}
export interface fetchStudentsRes extends IRes {
    data_list: dataItem[]
}

export const fetchStudents = (): Promise<fetchStudentsRes> => {
    return get({ url: '/students' });
}

export interface modifyStudentsRes extends IRes {

}

export const modifyStudent = (param: IStudent): Promise<modifyStudentsRes> => {
    return post({ url: '/students', body: { ...param, name: param.username } });
}

export const deleteStudent = (id: number): Promise<modifyStudentsRes> => {
    return delRequest({ url: '/students/' + id });
}


export interface fetchCourseDetailRes extends IRes {
    data_list: ICourseDetail[]
}

export const fetchCourseDetail = (id: number): Promise<fetchCourseDetailRes> => {
    return get({ url: '/courseenrollments/' + id });
}

export interface fetchMapInfoRes extends IRes {
    map_score_trend_date: string[]
    map_score_trend_value: string[]
    pdf1:string
    pdf2:string
    pdf3: string
    rit_score:string
    sub_domains_name: string[]
    sub_domains_score: string[]
    test_date: string
    test_duration:string
}

export const fetchMapInfo = (id: number): Promise<fetchMapInfoRes> => {
    return get({ url: '/stu_map_stats/' + id });
}

export interface modifyCourseDetailParams {
    stu_id: number,
    id?: number,
    course_id: string,
    course_user_name: string,
    course_user_password: string,
    course_school_code?: string,
    created?: string,
    ended_date?: string,
    description?: string,
    customer_service?: string
}

export const modifyCourseDetail = (params: modifyCourseDetailParams): Promise<IRes> => {
    return post({ url: '/courseenrollments', body: { ...params } });
}

export interface fetchAllCourses extends IRes {
    data_list: { id: string, display_name: string }[];
}

export const getCoursesList = (): Promise<fetchAllCourses> => {
    return get({ url: '/courses' });
}

export interface fetchAllServices extends IRes {
    data_list: { id: string, customer_service_name: string }[];
}

export const getServicesList = (): Promise<fetchAllServices> => {
    return get({ url: '/customerservices' });
}

export const deleteCourseDetail = ({ stu_id, id }: { stu_id: number, id: number }): Promise<modifyStudentsRes> => {
    return delRequest({ url: `/courseenrollments/${stu_id}/${id}`});
}

export const searchStudent = (value:string): Promise<fetchStudentsRes> => {
    return get({ url: `/stu_search/${value}`});
}