import { delRequest, get, post } from "../utils/rpc";
import { IRes, } from "./students";

export interface IServices {
    id: number,
    customer_service_name: string,
    customer_service_info: string,
    isModify: boolean
}

interface fetchServicesRes extends IRes {
    data_list: IServices[]
}

export const fetchServices = (): Promise<fetchServicesRes> => {
    return get({ url: '/customerservices' });
}

export const modifyService = (params: IServices): Promise<IRes> => {
    return post({ url: '/customerservices', body: { ...params } });
}

export const deleteService = (id: number): Promise<IRes> => {
    return delRequest({ url: `/customerservices/${id}` });
}


export interface ICcssitem {
    item_name: string,
    l3_grade: string,
    item_desc: string,
    l2_sub_domain: string
    isModify: boolean
    l1_domain: string,
    id:number
}

interface fetchCcssitemRes extends IRes {
    data_list: ICcssitem[]
}


export const fetchCcssitem = (): Promise<fetchCcssitemRes> => {
    return get({ url: '/ccssitem' });
}

export const modifyCcssitem = (params: ICcssitem): Promise<IRes> => {
    return post({ url: '/ccssitem', body: { ...params } });
}

export const deleteCcssitem = (id: number): Promise<IRes> => {
    return delRequest({ url: `/ccssitem/${id}` });
}