import React, { useEffect, useState } from "react";
import { ButtonWrap, Content, LeftWrap, RightWrap, SubTitle, Title, Wrap, } from "./styled";
import { Button } from "../../components/Button";
import LoadingPage from "../../components/loading";
import { useAllInfo, useProductInfo, } from "./action";
import { CustomSelect } from "../../components/CustomSelect";
import { ProductItem } from "./ProductItem";
import { modifyProductInfo } from "../../services/course";

export default () => {

  const [disabled, setDisabled] = useState(true);
  const { data } = useAllInfo();
  const [product, setProduct] = useState('');
  const { loading, checkedData, setCheckedData } = useProductInfo(product,disabled);
  

  useEffect(() => {
    if (data.productList.length > 0) {
      setProduct(data.productList[0].key);
    }
  }, [data.productList]);


  const checkList = [
    {
      title: 'GK',
      data: data.gkDate,
    },
    {
      title: 'G1',
      data: data.g1Date,
    },
    {
      title: 'G2',
      data: data.g2Date,
    },
    {
      title: 'G3',
      data: data.g3Date,
    },
    {
      title: 'G4',
      data: data.g4Date,
    },
    {
      title: 'G5',
      data: data.g5Date,
    },
    {
      title: 'G6',
      data: data.g6Date,
    },
    {
      title: 'G7',
      data: data.g7Date,
    },
    {
      title: 'G8',
      data: data.g8Date,
    },
    {
      title: 'G9-G10',
      data: data.g9_g10_items,
    },
    {
      title: 'G11-G12',
      data: data.g11_g12_items,
    },
  ];

  return (
    <Wrap>
      <Title style={{textAlign:'center'}}>产品匹配</Title>
      <ButtonWrap>
        {
          disabled ?
          <Button
            onPress={async () => {
              setDisabled(false);
            }}
            text='编辑'
            /> :
          <Button
            onPress={async () => {
                const params = {
                  course_overview: product,
                  check_items: checkedData
                };
                const res = await modifyProductInfo(params);
                if (res.executed&&res.executed) {
                  setDisabled(true);
                }
            }}
              text='确定'
          />
        }
      </ButtonWrap>
      <Content>
        <LeftWrap>
          <SubTitle>产品名称</SubTitle>
          <CustomSelect
            initValue={product}
            selectData={data.productList}
            onChange={(key) => {
              setProduct(key);
            }}
          />
        </LeftWrap>
        <RightWrap>
          {
            checkList.map((v, i) => (
              <ProductItem
                {...v}
                disabled={disabled}
                checkedData={checkedData}
                key={i}
                onChange={(checked: boolean, id: number) => {
                  if (checked) {
                    setCheckedData(pre => [...pre, id]);
                  } else {
                    const newCheckedData = checkedData.filter(v => v !== id);
                    setCheckedData(newCheckedData);
                  }
                }}               
              />
            ))
          }
        </RightWrap>
      </Content>
      <LoadingPage isLoading={loading} />
    </Wrap>
  )
};
