import styled from "styled-components";
import { FlexFill, CustomWrap } from "../styled";

export const Wrap = styled.div`
  width: 100%;
  padding: 30px;
`;

export const ButtonWrap = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  margin-right: 20px;
`;

export const Title = styled.div`
  font-size: 18px;
  font-weight: 600;
  text-align: center;
`;

export const ItemWrap = styled.div`
  margin-bottom: 30px;
`;

export const HeaderWrap = styled.div`
display:flex;
align-items:center;
justify-content:space-between;
flex-wrap:wrap;
width:1300px;
`

export const HeaderItem = styled.div`
width:50%;
font-size:18px;
`
export const HeaderText = styled.span`
color:#1789AD;
font-size:28px;
margin-left:10px;
`

export const Line = styled.div`
height:3px;
background-color:#eee;
margin:20px 0;
width:100%;
`
export const SubTitle = styled.div`
margin-bottom:20px;
font-size:20px;
font-weight:bolder;
color:#1789AD;
text-align:center;
`