import {
  Input,
  Popconfirm,
  Table,
  Button as AntButton,
  DatePicker,
  message,
  Tabs,
} from "antd";
import React, { useState } from "react";
import { Button } from "../../components/Button";
import { CustomSelect } from "../../components/CustomSelect";
import { ButtonWrap } from "./styled";
import {
  useCourseDetail,
  ICourseDetail,
  modifyCourseDetailAction,
  useMapInfo,
} from "./action";
import LoadingPage from "../../components/loading";
import { deleteCourseDetail, IListItem } from "../../services/students";
import "moment/locale/zh-cn";
import locale from "antd/es/date-picker/locale/zh_CN";
import moment from "moment";
import { Map } from "./Map";
import { PDFItem } from "./PDFItem";

const { TabPane } = Tabs;

interface ICourseProps {
  id: number;
  courseList: IListItem[];
  serviceList: IListItem[];
}

const schoolCodeList = [
  // {
  //     key: 'FlyHigh',
  //     value:'FlyHigh'
  // },
  // {
  //     key: 'Youle',
  //     value:'Youle'
  // },
  // {
  //     key: 'BananaApple',
  //     value:'BananaApple'
  // },
  {
    key: "China",
    value: "China",
  },
];

export const Course: React.FC<ICourseProps> = ({
  id,
  courseList,
  serviceList,
}) => {
  const [current, setCurrent] = useState(1);

  const [count, setCount] = useState(0);

  const { loading, setLoading, data, setData } = useCourseDetail(count, id);

  const { mapData } = useMapInfo(id);

  const handleChangeValue = (
    key: keyof ICourseDetail,
    value: any,
    item: ICourseDetail
  ) => {
    const newData = data.map((v) => {
      if (v.id === item.id) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setData(newData);
  };

  const columns = [
    {
      title: "学校代码",
      dataIndex: "course_school_code",
      key: "course_school_code",
      render: (value: string, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <CustomSelect
              initValue={value}
              selectData={schoolCodeList}
              onChange={(key) => {
                handleChangeValue("course_school_code", key, item);
              }}
            />
          );
        } else {
          return schoolCodeList.find((v) => v.key === value)?.value;
        }
      },
    },
    {
      title: "课程名称",
      dataIndex: "course_id",
      key: "course_id",
      render: (value: string, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <CustomSelect
              initValue={value}
              selectData={courseList}
              onChange={(key) => {
                handleChangeValue("course_id", key, item);
              }}
            />
          );
        } else {
          return courseList.find((v) => v.key === value)?.value;
        }
      },
    },
    {
      title: "账号",
      dataIndex: "course_user_name",
      key: "course_user_name",
      render: (value: string, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <Input
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("course_user_name", e.target.value, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "密码",
      dataIndex: "course_user_password",
      key: "course_user_password",
      render: (value: string, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <Input
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("course_user_password", e.target.value, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "生效日期",
      dataIndex: "created",
      key: "created",
      render: (value: any, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <DatePicker
              allowClear={false}
              placeholder="请选择生效日期"
              value={moment(value)}
              onChange={(date, dateString) => {
                handleChangeValue("created", dateString, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "失效日期",
      dataIndex: "ended_date",
      key: "ended_date",
      render: (value: any, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <DatePicker
              allowClear={false}
              placeholder="请选择失效日期"
              locale={locale}
              value={moment(value)}
              onChange={(date, dateString) => {
                handleChangeValue("ended_date", dateString, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "客服",
      dataIndex: "customer_service",
      key: "customer_service",
      render: (value: string, item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <CustomSelect
              initValue={value}
              selectData={serviceList}
              onChange={(key) => {
                handleChangeValue("customer_service", key, item);
              }}
            />
          );
        } else {
          return serviceList.find((v) => v.key === value)?.value;
        }
      },
    },
    {
      title: "操作",
      key: "name5",
      render: (item: ICourseDetail) => {
        if (item.isModify) {
          return (
            <>
              <Button
                text="确定"
                style={{ margin: "0 5px" }}
                onPress={async () => {
                  const params = {
                    ...item,
                    stu_id: id,
                  };
                  const res = await modifyCourseDetailAction(params);
                  if (res) {
                    setCount((v) => v + 1);
                  }
                }}
              />
              <Button
                style={{ margin: "0 5px" }}
                text="取消"
                onPress={async () => {
                  setCount((v) => v + 1);
                }}
              />
            </>
          );
        }
        return (
          <>
            <Button
              style={{ margin: "0 5px" }}
              text="修改"
              onPress={async () => {
                const newData = data.map((v) =>
                  v.id === item.id ? { ...v, isModify: true } : v
                );
                setData(newData);
              }}
            />
            <Popconfirm
              style={{ margin: "0 5px" }}
              title="确定删除此学生？"
              placement="topLeft"
              transitionName=""
              onConfirm={async () => {
                setLoading(true);
                const res = await deleteCourseDetail({
                  stu_id: id,
                  id: item.id,
                });
                setLoading(false);
                if (res.executed) {
                  setCount((v) => v + 1);
                }
              }}
            >
              <AntButton>删除</AntButton>
            </Popconfirm>
          </>
        );
      },
    },
  ];
  return (
    <div style={{ paddingTop: "30px" }}>
      <Tabs onChange={(key) => {}} type="card" size="large">
        <TabPane tab="课程信息" key="1">
          <ButtonWrap>
            <Button
              onPress={async () => {
                if (data.filter((v) => v.id === 0).length > 0) {
                  message.error("请先完成当前新增");
                  return;
                }
                setCurrent(1);
                setData((v) => [
                  {
                    id: 0,
                    course_id: "",
                    course_user_name: "",
                    course_user_password: "",
                    course_school_code: "",
                    created: moment().format("YYYY-MM-DD"),
                    ended_date: moment().format("YYYY-MM-DD"),
                    customer_service: "",
                    isModify: true,
                  },
                  ...v,
                ]);
              }}
              text="新增学生课程"
            />
          </ButtonWrap>
          <Table
            pagination={{
              hideOnSinglePage: true,
              current: current,
              onChange: (page) => {
                setCurrent(page);
              },
            }}
            rowKey={(item) => item.id}
            dataSource={data}
            columns={columns.map((v) => ({ ...v, align: "center" }))}
          />
        </TabPane>
        <TabPane tab="CCSS筛查报告" key="2">
          {/* <Map mapData={mapData}/> */}
          map报告展示
        </TabPane>
        <TabPane tab='PDF' key='3'>
            <div style={{margin:10}}>
              <a href={mapData.pdf2}  target='_blank'>CCSS图表(带编号)</a>
            </div>
            <div style={{margin:10}}>
              <a href={mapData.pdf3}  target='_blank'>CCSS图表(无编号)</a>
            </div>
            <div style={{margin:10}}>
              <a href={mapData.pdf1} target='_blank'>CCSS图表(简化版)学员报告</a>
            </div>
        </TabPane>
        {/* <TabPane tab="CCSS图表(带编号)" key="3">
          <PDFItem url={mapData.pdf2} />
        </TabPane>
        <TabPane tab="CCSS图表(无编号)" key="4">
          <PDFItem url={mapData.pdf3} />
        </TabPane>
        <TabPane tab="CCSS图表(简化版)学员报告" key="5">
          <PDFItem url={mapData.pdf1} />
        </TabPane> */}
      </Tabs>
      <LoadingPage isLoading={loading} />
    </div>
  );
};
