import React, { useEffect, useMemo, useState } from "react";
import { Wrap } from "./styled";
import { Input } from "antd";

interface Item {
  name: string;
  done: boolean;
  id: string;
}

interface IProps {
  item: Item;
}

const Child: React.FC<IProps> = React.memo(({ item }) => {
  console.log("123123");
  return <div>{item.name}</div>;
});

