import { message } from "antd";
import { useEffect, useState } from "react";
import {
  fetchStudents,
  IStudent,
  modifyStudent,
  fetchCourseDetail,
  getCoursesList,
  modifyCourseDetail,
  modifyCourseDetailParams,
  IListItem,
  getServicesList,
  searchStudent,
  fetchMapInfo,
} from "../../services/students";
import { validatePhone } from "../../utils/validate";

