export interface ICourseDetail {
  course_id: string;
  course_school_code: string;
  course_user_name: string;
  course_user_password: string;
  created: any;
  customer_service: string;
  ended_date: any;
  id: number;
  isModify: boolean;
}

export const useStudents = (count: number) => {
  const [data, setData] = useState<IStudent[]>([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    fetchStudents()
      .then((v) => {
        const newData = v?.data_list?.map((v) => ({
          ...v.user,
          phone_number: v.phone_number,
          web_accelerator_name: v.web_accelerator_name,
          web_accelerator_link: v.web_accelerator_link,
          isModify: false,
          courses_count: v.courses_count,
        }));
        newData.reverse();
        setData(newData);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [count]);

  return {
    loading,
    data,
    setData,
    setLoading,
  };
};

export const modifyStudentAction = async (params: IStudent) => {
  console.log(params);
  if (!params.username) {
    message.error("请输入用户名");
    return;
  }
  const reg = /^[-_0-9a-zA-Z\u4e00-\u9fa5]{1,}$/;
  if (!reg.test(params.username)) {
    message.error(
      "用户名只能由中英文、数字、-、和_组成，不能包含空格与其他特殊字符"
    );
    return;
  }
  if (!params.password) {
    message.error("请输入密码");
    return;
  }
  const phoneTemp = validatePhone(params.phone_number);
  if (!phoneTemp.state) {
    message.error(phoneTemp.msg);
    return;
  }
  const res = await modifyStudent(params);
  return res.executed;
};

export const useCourseDetail = (count: number, id: number) => {
  const [data, setData] = useState<ICourseDetail[]>([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    fetchCourseDetail(id)
      .then((v) => {
        const newData = v?.data_list.map((v) => ({
          ...v,
          created: v.created.split("T")[0],
          ended_date: v.ended_date.split("T")[0],
        }));
        newData.reverse();
        setData(newData);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [count, id]);

  return {
    loading,
    data,
    setData,
    setLoading,
  };
};

export interface IMapInfo {
  map_score_trend_date: string[];
  map_score_trend_value: string[];
  pdf1: string;
  pdf2: string;
  pdf3: string;
  rit_score: string;
  sub_domains_name: string[];
  sub_domains_score: string[];
  test_date: string;
  test_duration: string;
}

export const useMapInfo = (id: number) => {
    const [mapData, setMapData] = useState<IMapInfo>({
        map_score_trend_date: [],
        map_score_trend_value:[],
        pdf1:'',
        pdf2:'',
        pdf3:'',
        rit_score: '',
        sub_domains_name: [],
        sub_domains_score: [],
        test_date: '',
        test_duration: '',
  });
  useEffect(() => {
    fetchMapInfo(id).then((v) => {
      setMapData({
        map_score_trend_date: v.map_score_trend_date,
        map_score_trend_value: v.map_score_trend_value,
        pdf1: v.pdf1,
        pdf2: v.pdf2,
        pdf3: v.pdf3,
        rit_score: v.rit_score,
        sub_domains_name: v.sub_domains_name,
        sub_domains_score: v.sub_domains_score,
        test_date: v.test_date,
        test_duration: v.test_duration,
      });
    });
  }, [id]);

  return {
    mapData,
  };
};

export const modifyCourseDetailAction = async (
  params: modifyCourseDetailParams
) => {
  console.log(params);
  if (!params.course_id) {
    message.error("请选择课程名称");
    return;
  }
  if (!params.course_user_name) {
    message.error("请输入账号");
    return;
  }
  if (!params.course_user_name) {
    message.error("请输入密码");
    return;
  }
  const res = await modifyCourseDetail(params);
  return res.executed;
};

export const useCourseList = () => {
  const [courseList, setCourseList] = useState<IListItem[]>([]);
  useEffect(() => {
    getCoursesList().then((res) => {
      const temp = res.data_list.map((v) => ({
        key: v.id,
        value: v.display_name,
      }));
      setCourseList(temp);
    });
  }, []);

  return {
    courseList,
  };
};

export const useServiceList = () => {
  const [serviceList, setServiceList] = useState<IListItem[]>([]);
  useEffect(() => {
    getServicesList().then((res) => {
      const temp = res.data_list.map((v) => ({
        key: v.id,
        value: v.customer_service_name,
      }));
      setServiceList(temp);
    });
  }, []);

  return {
    serviceList,
  };
};

export const handleSearchResult = async (value: string) => {
  const res = await searchStudent(value);
  const newData = res?.data_list?.map((v) => ({
    ...v.user,
    phone_number: v.phone_number,
    web_accelerator_name: v.web_accelerator_name,
    web_accelerator_link: v.web_accelerator_link,
    isModify: false,
    courses_count: v.courses_count,
  }));
  newData.reverse();
  return newData;
};
