import { delRequest, get, post } from "../utils/rpc";
import { IRes, } from "./students";

export interface ICourse {
    id: string,
    org: string,
    display_name: string,
    course_outside: string,
    course_link: string,
    isModify: boolean,
    course_highlight: string,
    course_recommend_level: string,
    course_grade: string,
    course_price:string
}

export interface modifyCourseParams {
    course_overview: string,
    course_outside: string,
    course_link: string
}

interface ICourseItem {
    id: string,
    org: string,
    display_name: string,
    course_ext_info?: {
        course_outside: boolean,
        course_link: string,
        course_grade: string,
        course_price: string,
        course_recommend_level: string,
        course_highlight:string
    }
}

interface fetchCoursesRes extends IRes {
    data_list: ICourseItem[]
}

export const fetchCourses = (): Promise<fetchCoursesRes> => {
    return get({ url: '/courses' });
}

export const modifyCourse = (params: modifyCourseParams): Promise<IRes> => {
    return post({ url: '/courses', body: { ...params } });
}

export const deleteCourse = (id: string): Promise<IRes> => {
    return delRequest({ url: `/courses/${id}` });
}

export interface IItems {
    id: number,
    item_name:string
}
interface fetchAllProductInfoRes extends IRes{
    gk_items: IItems[],
    g1_items: IItems[],
    g2_items: IItems[],
    g3_items: IItems[],
    g4_items: IItems[],
    g5_items: IItems[],
    g6_items: IItems[],
    g7_items: IItems[],
    g8_items: IItems[],
    g9_g10_items: IItems[],
    g11_g12_items: IItems[],
    course_ids:{id:string,course_overview__display_name:string}[]
}

export const fetchAllProductInfo = (): Promise<fetchAllProductInfoRes> => {
    return get({ url: '/courses_ccss_items' });
}

interface fetchProductInfoRes extends IRes{
    added_items:number[]
}

export const fetchProductInfo = (id:string): Promise<fetchProductInfoRes> => {
    return get({ url: `/courses_ccss_items/${id}`  });
}

interface modifyProductInfoParams{
    course_overview: string,
    check_items:number[]
}

export const modifyProductInfo = (params:modifyProductInfoParams): Promise<IRes> => {
    return post({ url: `/courses_ccss_items` ,body:params});
}