import React, { useEffect, useState } from "react";
import { Input, } from 'antd';
import { CustomSelect } from "../CustomSelect";
import { InputWrap, InputLabel, InputListContent, InputListLabel } from "./styled";


interface ICustomInput {
  label?: string;
  initValue: string;
  onChange: (value: string) => void;
  isHidden?: boolean;
  placeholder?: string;
  style?: {};
  disabled?: boolean;
  maxLength?: number;
  inputStyle?: {};
  labelStyle?: {};
  type?: string;
}

export const CustomInput: React.FC<ICustomInput> = ({
  label,
  initValue,
  onChange,
  isHidden,
  inputStyle,
  labelStyle,
  style,
  ...other
}) => {

  const [value, setValue] = useState("");
  useEffect(() => {
    setValue(initValue);
    onChange && onChange(initValue);
  }, [initValue]);

  return !isHidden ? (
    <InputWrap style={style || {}}>
      {
        label &&
        <InputLabel style={labelStyle || {}}>{label}</InputLabel>
      }
      <Input
        style={inputStyle||{}}
        className='custom-input'
        value={value}
        onChange={(e) => { onChange(e.target.value); setValue(e.target.value);}}
        {...other}
      />
    </InputWrap >
  ) : null
}


interface IInputList {
  title?: string,
  style?: {},
  inputData: any[];
  inputWrapStyle?:{}
}

export const InputList: React.FC<IInputList> = ({
  title,
  style,
  inputData,
  inputWrapStyle
}) => {
  return (
    <div style={style || {}}>
      {
        title &&
        <InputListLabel>{title}</InputListLabel>
      }
      <InputListContent style={inputWrapStyle||{}}>
        {
          inputData.map((v, i) => {
            switch (v.type || "text") {
              case "text":
                return <CustomInput key={i} {...v} />;
              case "select":
                return <CustomSelect key={i} {...v} />;
            }
          })
        }
      </InputListContent>
    </div >
  )
}